.search-icon-container {
  width: 70px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  cursor: pointer;
}

.autotag-input-container {
  height: 50px;
  width: 60vw;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

  background: #fff;
  border-radius: 34px;
  padding: 10px 20px;
}

.autotag-input {
  height: 100%;
  width: 100%;
  border: 0;
  background: #fff;
  font-size: 18px;
  color: #666;
}

.autotag-input:focus {
  outline: none;
}

/** suggestions */

.autotag-suggestions {
  position: absolute;
  margin-left: 75px;
}

.autotag-suggestions ul {
  margin: 4px -1px;
  padding: 0;
  list-style: none;
  background: white;
  border: 1px solid #d1d1d1;
  border-radius: 2px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
}

.autotag-suggestions li {
  border-bottom: 1px solid #ddd;
  padding: 6px 8px;
}

.autotag-suggestions li mark {
  text-decoration: underline;
  background: none;
  font-weight: 600;
}

.autotag-suggestions li:hover {
  cursor: pointer;
  background: #eee;
}

.autotag-suggestions li.is-active {
  background: #b7cfe0;
}

.autotag-suggestions li.is-disabled {
  opacity: 0.5;
  cursor: auto;
}

/** Results */

.results-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-top: 15px;
  padding: 10px 20px;
  background: rgba(0, 0, 0, 0.8);
}

.tag-container {
  height: 40px;
  width: 100%;
  margin-bottom: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.tag {
  height: 100%;
  width: 50%;
  border-radius: 30px;
  padding: 0px 10px;
  background: #6c7ae0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.tag-name {
  font-size: 12px;
}

