}

.popcorn {
  margin: auto;
  height: 200px;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  background: #1e1e26;
}

circle#popcrncirlce {
  fill: #ffe477;
  transform-origin: 0 0;
  -webkit-transform-origin: 0 0;
  -webkit-animation: rotate 1.5s cubic-bezier(0.49, 0.05, 0.32, 1.04) infinite
    alternate;
  animation: rotate 1.5s cubic-bezier(0.49, 0.05, 0.32, 1.04) infinite alternate;
}

@-webkit-keyframes rotate {
  50% {
    -webkit-transform: translate(0px, 100px);
  }
}

@keyframes rotate {
  50% {
    -webkit-transform: translate(0px, 100px);
            transform: translate(0px, 100px);
  }
}

